
#news-items,#yesterday-news-items,#before-news{
    height: 90px;
    width: 100%;
    /*background-color:gray;*/
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    border-bottom: 1px solid rgba(0,0,0,0.1);
}
.content-text{
    height: 65px;
    width: 65%;
    /*background-color: #fff;*/
    font-weight: bolder;
    font-size: 14px;
    margin-top:-4px;
    margin-right:10px;
}
.content-img{
    height: 65px;
    width:24%;
    /*background-color: #fbb;*/
}
.content-img img{
    height: 100%;
    width: 100%;
}
/*btn*/
.yesterday-news,.before-tag{
    width:100%;
    height: 35px;
    background-color: #000;
    font-size: 14px;
    color:#fff;
    text-align: center;
    line-height: 35px;
    letter-spacing: 2px;
}
.demo-raised-button{
    border-radius: 0 !important;
    letter-spacing: 2px;
}



