.title-box{
    height: 38px;
    width: 100%;
    position: fixed;
    z-index: 3;
}
.title{
    display: block;
    margin:0 auto;
    height: 38px;
    width:200px;
    line-height: 38px;
    text-align: center;
    color:#fff;
    font-weight: bold;
    font-size: 18px;
}
.menu-btn{
    position: absolute;
    left:2%;
    top:10%;
}
#swiper{
    height: 200px;
}
#swiper img{
    margin-top: -40px;
    width: 100%;
}
.swiper-pagination{
    bottom:-1px !important;
}
.swiper-title{
    display: block;
    width: 75%;
    font-size: 16px;
    position: absolute;
    top:135px;
    left: 10%;
    color:#fff;
    font-weight: bolder;
    text-align: center;
}
/*shade*/
.shade-top,.shade-bottom{
    height: 30px;
    width:100%;
    position: absolute;
    z-index: 2;
}
.shade-top{
    top:0;
    background: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0));
}
.shade-bottom{
    bottom: 0;
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.3));
}
#swiper-box{
    position: relative;
}
.themes-title{
    font-size: 14px !important;
}
.mu-drawer{
    overflow: auto;
}
.head-title{
    font-size: 20px !important;
    font-weight: bold;
}



