
.theme-header{
    height: 38px;
    width: 100%;
    position: fixed;
    top:0;
    /**/
    overflow:hidden;
}
.theme-header-title{
    display: block;
    margin:0 auto;
    height: 38px;
    width:200px;
    line-height: 38px;
    text-align: center;
    color:#fff;
    font-weight: bold;
    font-size: 18px;
}
.theme-btn{
    position: absolute;
    left:2%;
    top:10%;
}
.theme-header img{
    position: absolute;
    top:0;
    z-index:-1; 
    -webkit-filter: blur(2px); 
            filter: blur(2px);
    height: 38px;
    width:100%;
}
/**/
.list-items{
    height: 90px;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    border-bottom: 1px solid rgba(0,0,0,0.1);
}
.list-item-title{
    height: 65px;
    width: 65%;
    font-weight: bolder;
    font-size: 14px;
    margin-top:-4px;
    margin-right:10px;
}
.list-item-img{
    height: 65px;
    width:24%;
}
.list-item-img img{
    height: 100%;
    width:100%;
}
.list-box{
    margin-top: 38px;
}



