
#comment[data-v-2be9b03a]{
    position: absolute;
    top:0;
    height: 100%;
    /*overflow: auto;*/
    background-color: #fff;
}
.comment-title[data-v-2be9b03a]{
    position: fixed;
    width:100%;
    height: 33px;
    background-color: #000;
    color:#fff;
    font-size:18px;
    letter-spacing: 2px;
    text-align: center;
    z-index:100;
}
.comment-title span[data-v-2be9b03a]{
    display: block;
    margin:0 auto;
    height: 33px;
    width:50%;
    line-height: 33px;
}
.comment-back[data-v-2be9b03a]{
    position: absolute;
    top:0;
    left:2%;
}
.mu-icon[data-v-2be9b03a]{
    line-height: 33px;
}
.fade-enter-active[data-v-2be9b03a], .fade-leave-active[data-v-2be9b03a] {
  transition: opacity .3s
}
.fade-enter[data-v-2be9b03a], .fade-leave-active[data-v-2be9b03a] {
  opacity: 0
}
/**/
.user[data-v-2be9b03a]{
    margin:40px auto 0 auto;
}
.user-detail[data-v-2be9b03a]{
    width:95%;
    /*border-bottom:solid;*/
    margin:5px auto;
}
.user-detail img[data-v-2be9b03a]{
    height: 37px;
    width:37px;
    border-radius: 50%;
    margin-left: 2%;
    margin-top:3%;
}
.user-name[data-v-2be9b03a]{
    display: inline-block;
    vertical-align: middle;
    margin-left: 5px;
    font-weight: bold;
}
.user-rating[data-v-2be9b03a]{
    display: inline-block;
    width:235px;
    margin-left: 17%;
    margin-top: -10px;
    margin-bottom: 10px;
    font-size: 12px;
    vertical-align: bottom;
}
.rating-time[data-v-2be9b03a]{
    margin-left: 17%;
    font-size: 12px;
}
.loading[data-v-2be9b03a]{
    display: block;
    margin:0 auto;
    margin-top:5px;
    margin-bottom:5px;
}



